import React,{useState} from 'react'
import "./sidebar.css"
import plusicon from '../assets/download.png'

function Sidebar(props) {

const colors =["#fe9b72","#fec971","#00d4fe","#b693fd","#e4ee91"]

const [list,setlist] = useState(false)


    return (
        <div className="sidebar">
           <img src={plusicon} alt="" onClick={()=> setlist(!list)}/>
           <ul className={`color-list ${list? "color-list-active": ""}`}>

           {colors.map((itm, index) => {
return(
                <li key={index} className="color-list-itm" style={{backgroundColor:itm}} onClick={()=>props.addnot(itm)}/>
                )
           })}


              
           </ul>
        </div>
    )
}

export default Sidebar
