import { useState,useEffect } from 'react';
import './App.css';
import Note from './note/note';
import Notecomponent from './notecomponent/notecomponent';
import Sidebar from './sidebar/sidebar';

function App() {


const [note,setnote] = useState(JSON.parse(localStorage.getItem('notes-app')) || []
);


const addnote = (color) =>{
const newnot = [...note]
newnot.push({
  id:Date.now + "" + Math.floor(Math.random()*78),
  text:"",
  time:Date.now(),
  color,
});

setnote(newnot);
};

const deletnote = (id) =>{
const delnot = [...note];
const indx = delnot.findIndex((item) => item.id === id)

 if (indx< 0) return;
 delnot.splice(indx,1)
 setnote(delnot)
}

const updateText = (text, id) => {
  const tempNotes = [...note];

  const index = tempNotes.findIndex((item) => item.id === id);
  if (index < 0) return;

  tempNotes[index].text = text;
  setnote(tempNotes);
};

useEffect(()=>{
localStorage.setItem('notes-app',JSON.stringify(note))
},[note])
  
  return (
    <div className="App">
      <Sidebar addnot={addnote}/>
      <Notecomponent notes={note} deletnote={deletnote} updateText={updateText}/>

    </div>
  );
}

export default App;
