import React from 'react'
import "./note.css"
import deleticon from '../assets/download (1).png'


function Note(props) {

     const {time,text,color} = props.notes

     let timer = 500,timeout

     const months = ['Jan','Fab','March','April','May','Jun','jul','Aug','Sept','Oct','Nov','Dec']



    const formatdate = (value)=>{

        const date = new Date(value)
        let hours = date.getHours()
        let ampm = hours>12?"PM":"AM"
        hours=hours?hours:"12"
        hours= hours>12?(hours=hours-12):hours

        let min = date.getMinutes()
        min=min<10?"0"+min:min
        let day = date.getDate()

        let moth = months[date.getMonth()]

        return `${hours}:${min} ${ampm} ${day} ${moth}`

    }

    const debounce = (func) => {
        clearTimeout(timeout);
        timeout = setTimeout(func, timer);
      };
    
      const updateText = (text, id) => {
        debounce(() => props.updateText(text, id));
      };
    
    return (
        <div className="note" style={{backgroundColor:color}}>
          <textarea  onChange={(event) => updateText(event.target.value, props.notes.id)} className="note-txt" >{text}</textarea>
          <div className="footer-itm">
          <p>{formatdate(props.notes.time)}</p>
              <img src={deleticon} alt="" onClick={()=>props.deletnote(props.notes.id)}/>
          </div>
        </div>
    )
}

export default Note
